.backdrop {
	position: fixed;
	height: 100%;
	width: 100%;
	z-index: 100;
	background: url(../Ui/Images/pizza-bg.jpg) center center;
	background-size: cover;
}

.cart-modal {
	position: fixed;
	top:50%;
	left: 50%;
	transform: translate3d(-50%, -37%, 0rem);
	z-index: 101;
	display: flex;
	justify-content: center;
	align-items: center;
}

