header {
	width: 100%;
	background-color: var(--cafe-oscuro);
	color: var(--crema);
	display: flex;
	justify-content: space-between;
	height: 12rem;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 100;
	filter: drop-shadow(.5rem .5rem 1rem rgba(0, 0, 0, .8));
}
header:before {
	content: "";
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	background: url('../Ui/Images/textura.svg');
	background-size: 35% auto;
	opacity: 0.2;
	z-index: -1;
}

.brand {
	display: inline-block;
	padding:  0 2rem;
	background-color: var(--naranja);
}

.logo {
	width: 20rem;
}
.slogan {
	margin-left: .5rem;
	font-size: 2rem;
	color: var(--crema);
}

.cart-wrap {
	margin-right: 6rem;
	display: flex;
	align-items: center;
	justify-content: center;
}
