.meals-meal__item {
	width: 100%;
	display: inline-block;
	display: grid;
	grid-template-columns: 8rem 1fr max-content;
	grid-gap: 1rem;
	padding: .5rem;
	border-radius: .5rem;
}
.meals-meal__item:nth-child(even) {
	background-color: var(--blanco);
}
.meals-meal__item:not(:last-child) {
	margin-bottom: 1rem;
}

.meals-meal__icon img {
	display: inline-block;
	vertical-align: middle;
}

.meals-meal__name {
	text-transform: uppercase;
	font-size: 2rem;
	font-family: 'Roboto Condensed', sans-serif;
}

.meals-meal__information {
	display: flex;
	flex-flow: column;
	gap:  .5rem;
}

.meals-meal__description {
	font-size: 1.6rem;
}

.meals-meal__price {
	font-weight: 600;
	font-size: 2rem;
	font-family: 'Roboto Condensed', sans-serif;
	color: var(--naranja);
	letter-spacing: -0.1rem;
}
.meals-meal__form form {
	display: grid;
	grid-gap: .5rem;
	grid-template-columns: max-content 4rem;
}
