.cart {
	color: var(--negro);
	border-radius: 1rem;
	padding: 0.8rem;
	background-color: var(--blanco);
	display: flex;
	gap: 1rem;
	cursor: pointer;
	font-weight: 600;
	transition: transform 200ms ease;
	transform: translate3d(0px, 0px, 0px);
}
.cart:hover,
.cart:focus {
	background-color: var(--crema);
	color:  var(--naranja);
	filter: drop-shadow(.5rem .5rem .5rem rgba(0, 0, 0, .7));
	transform: translate3d(-0.3rem, -0.3rem, 0px) ;
}

.cart:active {
	transform: translate3d(0rem, 0rem, 0px) ;
}

.cart-icon {
	width: 3rem;
	margin-left: 1rem;
}

.cart-lable,
.cart-cuantity {
	font-size: 1.5rem;
	display: flex;
	align-items: center;
	justify-content: center;
	-moz-user-select: none; 
	-webkit-user-select: none; 
	-ms-user-select:none; 
	user-select:none;
	-o-user-select:none;
}

.cart-cuantity {
	margin-right: 1rem;
}

.cart-cuantity span {
	display: inline-block;
	background: var(--cafe-oscuro);
	color: var(--crema);
	border-radius: .5rem;
	padding: 0.5rem 1rem;
}

.bump {
	animation: bump ease-out 300ms;
}

@keyframes bump {
	0% {
		transform: scale(1);
	}
	10% {
		transform: scale(0.9);
	}
	30% {
		transform: scale(1.1);
	}
	50% {
		transform: scale(1.15);
	}
	100% {
		transform: scale(1);
	}
}