.cart-content {
	background-color: var(--blanco);
	min-width: 48rem;
	filter: drop-shadow(.5rem .5rem 1rem rgba(0, 0, 0, .8));
}

.cart-content__title {
	font-family: 'Roboto Condensed', sans-serif;
	font-size: 2.5rem;
	text-transform: uppercase;
	padding: 1rem 2rem;
	background-color: var(--naranja);
	color: var(--blanco);
	border-bottom:solid 0.4rem var(--cafe-oscuro);
	display: flex;
	align-items: center;
	justify-content: space-evenly;
}
.cart-content__title .cart-icon {
	width: 4rem;
	margin-right: 2rem;
}
.cart-content__title .cart-icon svg path {
	fill:  var(--blanco);
}

.cart-content__content {
	padding: 1rem 2rem;
}
.cart-content__items {
	max-height: 27rem;
	overflow-y: scroll;
	display: block;
	padding-right: .6rem;
}
.cart-content__none {
	font-family: 'Roboto Condensed', sans-serif;
	font-size: 2rem;
	margin: .8rem 0rem;
}
.cart-content__amount {
	font-size: 2.5rem;
	padding-top: 1rem;
	border-top: solid .2rem var(--naranja);
	display: flex;
	justify-content: space-between;
}
.cart-content__total {
	font-weight: 600;
	color: var(--naranja);
}
.cart-content__control {
	float: right;
	margin: 1rem 0rem;
	display: flex;
	gap: 1rem;
}
.cart-content__control .cart-content__order,
.cart-content__control .cart-content__close {
	font-size: 1.6rem;
	padding: 1rem 2rem;
	border-radius: 2.5rem;
}

.cart-content__control .cart-content__order:hover,
.cart-content__control .cart-content__order:focus {
	background-color: var(--cafe-oscuro);
}	
.cart-content__close:hover, 
.cart-content__close:focus {
	background-color: var(--cafe-oscuro);
	color: var(--blanco);
} 

.cart-content__order:active,
.cart-content__close:active {
	transform: translate3d(0.1rem, 0.1rem, 0px);
}

.cart-content__control .cart-content__order {
	background-color: var(--naranja);
	color: var(--blanco);
}