form {
	position: relative;
}
form label {
	font-size: 2rem;
	font-family: 'Roboto Condensed', sans-serif;
	letter-spacing: -0.1rem;
	padding: .5rem;
	margin: auto 0;
}
form input {
	height: 3rem;
	width: 4rem;
	text-align: center;
	margin: auto;
}

form .meals-meal__submit {
	grid-column: span 2;
	background-color: var(--naranja);
	color: var(--blanco);
	border: none;
}
form .meals-meal__submit:hover,
form .meals-meal__submit:focus {
	background-color: var(--cafe-oscuro);
}
form .meals-meal__submit:active {
	background-color: var(--naranja);
	transform: translate3d(.1rem, .1rem, 0px);
}

.meals-meal__error {
	position: absolute;
	right: 105%;
	top: -1rem;
	width: max-content;
	background-color: rgb(255, 0, 0);
	color: var(--blanco);
	border-radius: .5rem;
	padding: 1rem;
	font-size: 1.3rem;
}