@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@400;600&family=Roboto+Condensed:wght@700&display=swap');

:root {
  --crema: #F9EDDC;
  --cafe-oscuro: #22160A;
  --naranja: #F46425;
  --error: #EA0027;
  --negro:#000814;
  --deshabilitado: #183A48;
  --blanco: #fff;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

input {
  font-family: 'Quicksand', sans-serif; 
}
input:focus,
input:focus-visible {
  outline: none;
}

html {
  font-size: 62.5%;
  /*font-family: 'Roboto Condensed', sans-serif;*/
}

body {
  font-family: 'Quicksand', sans-serif;
  background-color: var(--cafe-oscuro);
}
body:before {
  content: "";
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: url('./components/Ui/Images/textura.svg');
  background-size: 35% auto;
  opacity: 0.2;
  z-index: -1;
}