.svg-containter svg {
	width: 100%;
}
.svgicon {
	fill:var(--cafe-oscuro);
	stroke:var(--cafe-oscuro);
	stroke-width:0.25;
	stroke-linecap:round;
	stroke-linejoin:round;
	stroke-miterlimit:10;
}
.barra-naranja {
	fill:var(--naranja);
}
.barra-crema {
	fill:var(--crema);
}
.barra-blanca {
	fill: var(--blanco);
}
.deli1 {
	fill:var(--cafe-oscuro);
	stroke:var(--cafe-oscuro);
	stroke-width:0.75;
	stroke-miterlimit:10;
}
.deli2 {
	fill:var(--cafe-oscuro);
}
.deli3 {
	fill:var(--cafe-oscuro);
	stroke:var(--cafe-oscuro);
	stroke-width:1.5;
	stroke-miterlimit:10;
}
.pizzalogo-main {
	fill: var(--cafe-oscuro);
	stroke: var(--cafe-oscuro);
	stroke-width:0.1;
}
.cart-main {
	fill: var(--cafe-oscuro);
	stroke: transparent;
}
