.cart-orderform {
	margin-left: 2rem;
	display: grid;
	grid-template-columns: repeat(2, 35rem);
}

form.cart-orderform__form {
	position: relative;
	background-color: rgba(34, 23, 14, .8);
	border-radius: .8rem;
	padding: 1rem;
	filter: drop-shadow(.5rem .5rem 1rem rgba(0, 0, 0, .8));
}
@supports(backdrop-filter: blur(1rem)) {
	form.cart-orderform__form {
		background-color: rgba(34, 23, 14, .2);
		backdrop-filter: blur(1rem);
	}
}
.cart-orderform form.cart-orderform__form input {
	width: 90%;
	height: 3rem;
	display: inline-block;
	margin: 1rem 5%;
	border: none;
	text-align: left;
	text-indent: .2rem;
	font-size: 1.8rem;
	transition: color 1s ease;
	border-left: 1rem solid var(--blanco);
}
.cart-orderform form.cart-orderform__form input.invalid {
	border-left: 1rem solid var(--error);
}
.cart-orderform .cart-orderform__bg-image {
	transform: translateX(-2rem);
	filter: drop-shadow(.5rem .5rem 1rem rgba(0, 0, 0, .8));
	z-index: -1;
}
.cart-orderform__error {
	width: 90%;
	height: 2rem;
	margin: -1rem 5% 1rem 5%;
	border-bottom-left-radius: .5rem;
	border-bottom-right-radius: .5rem;
	background-color: var(--error);
	text-align: center;
	font-size: 1.6rem;
	color: var(--blanco);
}
.cart-orderform__button,
.cart-orderform__cancel {
	width: 90%;
	font-family: 'Roboto Condensed', sans-serif;
	color: var(--blanco);
	font-size: 2rem;
	display: inline-block;
	margin: 1rem 5%;
	background-color: var(--naranja);
}
.cart-orderform__cancel {
	background-color: var(--cafe-oscuro);
}

.cart-orderform__button:active,
.cart-orderform__cancel:active {
	transform: translate3d(.3rem, .3rem, 0px);
}

.cart-orderform__button:disabled {
	background-color: var(--deshabilitado);
	opacity: .8;
	cursor: not-allowed;
}

.cart-orderform__overlay {
	background-color: var(--blanco);
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	border-radius: .5rem;
}
.cart-orderform__success-order {
	font-size: 3rem;
	text-align: center;
	width: 90%;
	font-family: 'Roboto Condensed', sans-serif;
	margin: 1rem 5%;
}