.cart-item {
	display: flex;
	justify-content: space-between;
	padding-bottom: 1rem;
}

.cart-item:not(:last-child) {
	border-bottom: thin solid var(--cafe-oscuro);
	margin-bottom: 1rem;
}

.cart-item__information {
	display: grid;
	grid-template-columns: 5.5ch 3ch 1fr;
	font-size: 1.8rem;
	margin-right: 1.5rem;
	grid-gap: 1rem;
}

.cart-item__information >div {
	padding: .2rem;
}
.cart-item__prize {
	font-style: italic;
}
.cart-item__amount {
	text-align: center;
	border-radius: .5rem;
	border: thin solid var(--cafe-oscuro);
}
.cart-item__subtotal {
	margin-left: .5rem;
	border-left: dashed thin var(--cafe-oscuro);
	text-indent: 1rem;
	color: var(--naranja);
	font-weight: 600;
}
.cart-item__name {
	grid-column: 1 / -1;
	font-family: 'Roboto Condensed', sans-serif;
}
.cart-item__control {
	margin-right: 1rem;
	display: flex;
	flex-flow: column;
}

.cart-item__control .cart-item__btn {
	padding: 0.3rem 1.6rem;
	font-size: 1.6rem;
	font-weight: 600;
	font-family: 'Roboto Condensed', sans-serif;
}
.cart-item__control .cart-item__btn:not(:last-child) {
	margin-bottom: 1rem;
}
.cart-item__control .cart-item__btn:hover,
.cart-item__control .cart-item__btn:focus {
	background-color: var(--naranja);
	color: var(--blanco);
}
.cart-item__control .cart-item__btn:active {
	transform: translate3d(.2rem, .2rem, 0px);
}
